body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
}

.app {
  background-color: #000000;
  position: relative;
}

.row {
  color: #fff;
  margin-left: 20px;
}

.row__posters {
  display: flex;
  overflow-x: scroll;
  overflow-y: hidden;
  padding: 20px 0 20px 0;
  scroll-behavior: smooth;
}
.row__coletanea{
  padding: 20px 0 20px 0;
}

.row__posters::-webkit-scrollbar {
  display: none;
}

.row__poster {
  margin-right: 10px;
  max-height: 100px;
  object-fit: contain;
  opacity: 0.8;
  transition: transform 0.45s;
  width: 100%;
  position: relative;
}

.row__poster:hover {
  cursor: pointer;
  opacity: 1;
  transform: scale(1.08);
}

.row__posterLarge {
  max-height: 250px;
}

.row__posterLarge:hover {
  opacity: 1;
  transform: scale(1.09);
}

.shinji{
  background-image: url("https://www.themoviedb.org/t/p/original/5kScBac0tGJxcEHDds7xB5HjIqX.jpg");
  width: 100%;
}
.log-out-btn{
  background-color: #ff000080;
  border: none;
  border-radius: 6vw;
  color: #fff;
  cursor: pointer;
  font-weight: 700;
  outline: none;
  padding: 1.2rem 1.2rem;
  position: absolute;
  left: 96%;

}
.banner {
  background-position: center center;
  background-size: cover;
  color: #fff;
  height: 500px;
  object-fit: contain;
  width: 100%;
}



.banner__contents {
  height: 190px;
  margin-left: 30px;
  padding-top: 140px;
}

.banner__title {
  font-size: 3rem;
  font-weight: 800;
  padding-bottom: 0.3rem;
}

.banner__description {
  font-size: 16px;
  height: 150px;
  line-height: 1.3;
  line-height: 24px;
  max-width: 360px;
  padding-top: 1rem;
  width: 45rem;

}

.banner__button {
  background-color: #33333380;
  border: none;
  border-radius: 6vw;
  color: #fff;
  cursor: pointer;
  font-weight: 700;
  margin-right: 1rem;
  outline: none;
  padding: 1rem 1rem;
}

.banner__button:hover {
  background-color: #e6e6e6;
  color: #000;
  transition: all 0.2s;
}

.banner--fadeBottom {
  background-image: linear-gradient(
    180deg,
    transparent,
    rgba(37, 37, 37, 0.61),
    #111
  );
  height: 10.7rem;
}

.nav {
  background: #000000;
  display: flex;
  height: 30px;
  justify-content: space-between;
  padding: 20px;
  position: fixed;
  top: 0;
  transition: all 0.5s;
  transition-timing-function: ease-in;
  width: 100%;
  z-index: 1;
}

.nav__black {
  background-color: #000000;
}

.nav__logo {
  left: 20px;
  width: 80px;
}

.nav__avatar,
.nav__logo {
  object-fit: contain;
  position: fixed;
}

.nav__avatar {
  right: 20px;
  width: 30px;
}

.main {
  background: url("https://assets.nflxext.com/ffe/siteui/vlv3/11c803d4-06f3-467e-91f7-0560ce15a396/5ac22c62-f87e-45e3-8746-418d647185c9/VN-en-20210517-popsignuptwoweeks-perspective_alpha_website_small.jpg");
  background-size: cover;
  min-height: 100vh;
}

.login-body {
  align-items: center;
  display: grid;
  justify-content: center;
  min-height: 100vh;
}

.login-body__form {
  background-color: #000000cc;
  box-sizing: border-box;
  margin-top: 62px;
  max-width: 420px;
  padding: 48px 62px;
}

.login-body h1 {
  color: #fff;
  font-size: 32px;
  font-weight: 700;
  margin-bottom: 28px;
}

.login-body__input input {
  background: #333;
  border: 0;
  border-radius: 4px;
  color: #fff;
  font-size: 16px;
  outline: none;
  padding: 16px 20px;
  width: 86%;
}

.mb-16 {
  margin-bottom: 16px;
}

.login-body__submit-btn {
  background: #05358f;
  border: 0;
  border-radius: 35px;
  color: #fff;
  font-size: 16px;
  font-weight: bold;
  margin-top: 32px;
  outline: none;
  padding: 16px;
  width: 100%;
}

.login-body__submit-btn:hover {
  cursor: pointer;
}

.login-body__options {
  color: #b3b3b3;
  display: grid;
  font-size: 13px;
  font-weight: 500;
  grid-template-columns: repeat(2, 1fr);
  margin-bottom: 48px;
  margin-top: 12px;
}

.login-body__options:hover {
  cursor: pointer;
}

.login-body__need-help {
  text-align: right;
}

.login-body__fb {
  align-items: center;
  color: #b3b3b3;
  display: grid;
  font-size: 13px;
  grid-template-columns: min-content auto;
}

.login-body__fb:hover {
  cursor: pointer;
}

.login-body__fb img {
  height: 20px;
  margin-right: 12px;
  width: 20px;
}

.login-body__new-to-nl {
  color: #737373;
  font-size: 16px;
  font-weight: 500;
  margin-top: 16px;
}

.login-body__sign-up {
  color: #fff;
}

.login-body__sign-up:hover {
  cursor: pointer;
}

.login-body__google_captcha {
  color: #8c8c8c;
  font-size: 13px;
  margin-top: 16px;
}

.login-body__learn-more {
  color: #0071eb;
}

.login-body__learn-more:hover {
  cursor: pointer;
}

.detail {
  background: #111;
  bottom: 0;
  position: fixed;
  right: 0;
  top: 0;
  width: 450px;
  z-index: 999;
}

.detail__image {
  align-items: end;
  background-position: center center;
  background-size: cover;
  display: grid;
  height: 282px;
  width: 100%;
}

.detail__title {
  color: #fff;
  font-size: 2rem;
  font-weight: bold;
  padding-bottom: 24px;
  padding-left: 24px;
  z-index: 3;
}

.detail__fade-image {
  position: absolute;
  top: 164px;
  width: 100%;
}

.detail__actions {
  padding: 12px;
  padding-left: 24px;
}

.detail__btn {
  background: #e50914;
  border: none;
  border-radius: 4px;
  color: #fff;
  font-weight: 600;
  outline: none;
  padding: 8px 32px;
}

.detail__btn:hover {
  background: #e74c3c;
}

.detail__btn:hover {
  cursor: pointer;
}

.detail__description {
  color: #fff;
  padding: 12px;
  padding-left: 24px;
  padding-right: 24px;
  text-align: justify;
}

.mgr-8 {
  margin-right: 8px;
}

.detail__description-title {
  border-bottom: 2px solid #e50914;
  display: inline-block;
  font-weight: 600;
  margin-bottom: 12px;
  padding: 12px 0px;
}

.detail__description-content {
  font-size: 14px;
}
.bookmark{
  padding: 1rem 1.2rem;
}
.text-link {
  color: inherit;
  text-decoration: inherit;
}
.rowButton{
  background-color: #33333357;
  border: none;
  border-radius: 6vw;
  color: #fff;
  cursor: pointer;
  font-weight: 700;
  margin-right: 1rem;
  outline: none;
  padding: 1rem;
  margin-top: 6rem;
  position: absolute;
}
.right{
  right: 0;
}
.rowButton:hover{
  transform: scale(1.1);
}
.row__coletaneas {
  margin-right: 10px;
  max-height: 100px;
  object-fit: contain;
  opacity: 0.8;
  transition: transform 0.45s;
  position: relative;
}

.row__coletaneas:hover {
  cursor: pointer;
  opacity: 1;
  transform: scale(1.08);
}

.row__coletaneasLarge {
  max-height: 200px;
}

.row__coletaneas:hover {
  opacity: 1;
  transform: scale(1.09);
}
.coletanea{
  margin-top: 70px;
  margin-left: 60px;
  min-height: 869px;
  max-height: 4000px;
}
@media only screen and (max-width: 500px) {
  body{
    background: black;
  }
  .main{
    background: black;
  }
  .log-out-btn{
    left: 84%;
  }
  .banner__description{
    width: 90%;
  }
  .coletanea{
    margin-left: 20px;
    min-height: auto;
  }
  
  .login-body__form {
  background-color: #000000;
  margin-top: 0;
}
}
@media only screen and (max-width: 1800px) and (min-width:1001px) {
 
  .log-out-btn{
    left: 94%;
  }
}
@media only screen and (max-width: 1000px) and (min-width:731px) {
 
  .log-out-btn{
    left: 91%;
  }
}
@media only screen and (max-width: 730px) and (min-width:569px) {
 
  .log-out-btn{
    left: 90%;
  }
}
@media only screen and (max-width: 569px) and (min-width:501px) {
 
  .log-out-btn{
    left: 89%;
  }
}